<template>
  <div class="about">
    <h1>{{id?'':''}}</h1>
    <el-form
      label-width="70px"
      @submit.native.prevent="save"
    >
      <el-form-item label="">
        <el-input v-model="model.name"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-upload
          :headers="getAutorizationHeaders()"
          class="avatar-uploader"
          :action="uploadUrl"
          :show-file-list="false"
          :on-success="afterUpload"
          name="icon"
        >
          <img
            v-if="model.icon"
            :src="model.icon"
            class="avatar"
          >
          <i
            v-else
            class="el-icon-plus avatar-uploader-icon"
          ></i>
        </el-upload>
      </el-form-item>
      <el-form-item>
        <el-button
          type="primary"
          native-type="submit"
        ></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { putItemModel, postItem, getItemModel } from '@/api/api'
export default {
  props: ['id'],
  data() {
    return {
      model: {},
      parents: []
    }
  },
  methods: {
    afterUpload(res) {
      console.log(res);
      // this.model.icon = res.url
      //       set 
      this.$set(this.model, 'icon', res.url)
    },
    // 
    async fetch() {
      // const res = await this.$http.get(`categories/${this.id}`);
      const res = await getItemModel(this.id)
      this.model = res.data;
    },
    // 
    fetchCreate() {
      this.model = {}
    },

    //  axios    ()
    async save() {
      // let res
      if (this.id) {
        // await this.$http.put(`categories/${this.id}`, this.model);
        // 
        // id 
        await putItemModel(this.id, this.model)
        this.$message({
          type: 'success',
          message: ''
        })
      } else {
        // await this.$http.post('categories', this.model);
        // 
        await postItem(this.model)
        this.$message({
          type: 'success',
          message: ''
        })
      }

      this.$router.push('/items/list')
    }
  },
  watch: {
    '$route.path': function () {
      // alert(newVal + '-----' + oldVal)
      this.fetchCreate()
    }
  },
  created() {
    this.id && this.fetch();
  },
}
</script>

<style>
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>