<template>
  <div>
    <h1></h1>
    <el-table :data="items">
      <el-table-column
        prop="_id"
        label="ID"
        width="250"
      ></el-table-column>
      <el-table-column
        prop="name"
        label=""
        width="140"
      >
      </el-table-column>
      <el-table-column
        fixed="right"
        label=""
        width="400"
      >
        <template slot-scope="scope">
          <el-button
            @click="handleClickFatherModel(scope.row)"
            type="info"
            size="small"
          ></el-button>
          <el-button
            type="primary"
            size="small"
            @click="handleClickEditOrDel(scope.row,0)"
          ></el-button>
          <el-button
            type="primary"
            size="small"
            @click="$router.push(`/categories/createFather/edit/${scope.row._id}`)"
          ></el-button>
          <el-button
            type="danger"
            size="small"
            @click="handleClickEditOrDel(scope.row,1)"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--   -->
    <category-dialog
      ref="dialgComponent"
      :dialog-title="dialogTitle"
      :item-model="categoryModelFather"
      :dialog-type="dialogType"
      :router-type="routerType"
      :dialog-titlea="dialogTitleA"
      @fetch="fetch"
    >

    </category-dialog>
  </div>
</template>
<script>
import {  getCategoryFatherList, getCategoryFatherModel,
delCategoryFatherModel } from '@/api/api'
import CategoryDialog from '../components/Category/CategoryDialog'
export default {
  components: { CategoryDialog },
  data() {
    return {
      items: [],
      // 
      categoryModelFather: {},

      // dialog
      // dialogShowModel:false,
      // dialog
      dialogTitle: "",
      dialogType: null,// true false   
      //   false
      routerType: 'CategoryListFather',
      dialogTitleA:''
    }
  },
  methods: {
    async fetch() {
      const res = await getCategoryFatherList();
      this.items = res.data
    },
    //  
    dialogOff() {
      this.$nextTick(() => {
        this.$refs["dialgComponent"].dialogShowModel = true;

      })
    },
    // 
    async handleClickFatherModel(row) {
      this.dialogOff();
      //  async await  promise
      const { data: res } = await getCategoryFatherModel(row._id);
      // if(status!==200){
      //   return this.$message.error("")
      // }
      this.dialogTitle = ""
      this.categoryModelFather = res;
      this.dialogType = true;

    },
    //  id dialog1    0
    handleClickEditOrDel(row, id) {
      if (id == 0) {
        this.dialogOff();
        this.dialogTitle = "";
        this.categoryModelFather = row;
        this.dialogType = false;
      } else {
        this.$confirm(`${row.name}?`, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'error'
        }).then(async () => {
          await delCategoryFatherModel(row._id);
          this.$message({
            type: 'success',
            message: ''
          });
          this.fetch();
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })

        // this.fetch();
      }
    },
    //    
    closeDialog(flag) {
      if (flag) {
        // 
        this.fetch()
      }
    }
  },
  created() {
    this.fetch()
  },
}
</script>