<template>
  <div class="about">
    <h1>{{id?'':''}}</h1>
    <el-form label-width="150px" @submit.native.prevent="save">
      <el-tabs type="border-card">
        <!--  -->
        <el-tab-pane label="">
          <el-form-item label="">
            <el-input v-model="model.name"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="model.title"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-upload
              :headers="getAutorizationHeaders()"
              class="avatar-uploader"
              :action="$http.defaults.baseURL + '/upload'"
              :show-file-list="false"
              :on-success="afterUpload"
              name="icon"
            >
              <img v-if="model.avatar" :src="model.avatar" class="avatar" />
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>
          <el-form-item label="">
            <el-upload
              :headers="getAutorizationHeaders()"
              class="avatar-uploader"
              :action="uploadUrl"
              :show-file-list="false"
              :on-success="res=> $set(model,'drawing',res.url)"
              name="icon"
            >
              <img v-if="model.drawing" :src="model.drawing" class="avatar" />
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="model.attribute.name" placeholder="">
              <el-option
                v-for="item of attributes"
                :key="item._id"
                :label="item.name"
                :value="item._id"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="model.categories" placeholder="" multiple>
              <el-option
                v-for="item of categories"
                :key="item._id"
                :label="item.name"
                :value="item._id"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="HP">
            <el-input v-model="model.hpandattack.hp" class="input_left" placeholder="HP"></el-input>
            <el-input v-model="model.hpandattack.attack" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="model.race" placeholder="">
              <el-option v-for="item of races" :key="item._id" :label="item.name" :value="item._id"></el-option>
            </el-select>
          </el-form-item>
          <!-- otherinfo  -->
          <el-form-item label="">
            <el-input v-model="model.otherinfo.japanName"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="model.otherinfo.enName"></el-input>
          </el-form-item>
          <el-form-item label="()">
            <el-input v-model="model.otherinfo.commentName"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input type="textarea" v-model="model.otherinfo.notepad" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="model.otherinfo.age"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="model.otherinfo.height"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="model.otherinfo.cv"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="model.otherinfo.interest"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="model.otherinfo.hobby"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="model.otherinfo.nogoodat"></el-input>
          </el-form-item>
          <!-- otherinfo  -->
          <el-form-item label="">
            <el-select v-model="model.character" placeholder="" multiple>
              <el-option
                v-for="item of characteies"
                :key="item._id"
                :label="item.name"
                :value="item._id"
              ></el-option>
            </el-select>
          </el-form-item>
        </el-tab-pane>
        <!--  -->
        <el-tab-pane label="">
          <el-button @click="model.skills.push({})">
            <i class="el-icon-plus"></i>
          </el-button>
          <el-button type="danger" @click="model.skills.pop()">
            <i class="el-icon-delete"></i>
          </el-button>
          <el-row type="flex" style="flex-wrap:wrap; margin-top:15px; ">
            <el-col :md="12" v-for="(item,i) in model.skills" :key="i">
              <el-form-item label="/">
                <el-select v-model="item.passive" placeholder="">
                  <el-option
                    v-for="item in attributes"
                    :key="item._id"
                    :label="item.name"
                    :value="item.name"
                  ></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="">
                <el-upload
                  :headers="getAutorizationHeaders()"
                  class="avatar-uploader"
                  :action="$http.defaults.baseURL + '/upload'"
                  :show-file-list="false"
                  :on-success="res => $set(item,'icon',res.url)"
                  name="icon"
                >
                  <img v-if="item.icon" :src="item.icon" class="avatar" />
                  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="item.name"></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="item.time"></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="item.time2"></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input type="textarea" v-model="item.description"></el-input>
                <el-button type="danger" class="skills-button" @click="model.skills.splice(i,1)">
                  <i class="el-icon-delete"></i>
                </el-button>
              </el-form-item>
            </el-col>
          </el-row>
        </el-tab-pane>
        <!--  1 -->
        <el-tab-pane label="1">
          <el-row type="flex" style="flex-wrap:wrap; margin-top:15px; ">
            <el-col style="border:none;">
              1  
              <el-button @click="model.items1.push({})">
                <i class="el-icon-plus"></i>
              </el-button>
              <el-col v-for="(item,i) in model.items1" :key="i">
                <el-col style="border:none;">
                  <el-form-item :label="`1 ${i+1}`">
                    <el-select
                      filterable
                      v-model="item.article"
                      placeholder=""
                      style="width:200px;margin-right:100px"
                    >
                      <el-option
                        v-for="item in articles"
                        :key="item._id"
                        :label="item.name"
                        :value="item._id"
                      ></el-option>
                    </el-select>
                    <el-input v-model="item.num" style="width:100px"></el-input>
                  </el-form-item>
                </el-col>
              </el-col>
            </el-col>
          </el-row>
        </el-tab-pane>
        <!--  2 -->
        <el-tab-pane label="2">
          <el-row type="flex" style="flex-wrap:wrap; margin-top:15px; ">
            <el-col style="border:none;">
              2  
              <el-button @click="model.items2.push({})">
                <i class="el-icon-plus"></i>
              </el-button>
              <el-col v-for="(item,i) in model.items2" :key="i">
                <el-col style="border:none;">
                  <el-form-item :label="`2 ${i+1}`">
                    <el-select
                    filterable
                      v-model="item.article"
                      placeholder=""
                      style="width:200px;margin-right:100px"
                    >
                      <el-option
                        v-for="item in articles"
                        :key="item._id"
                        :label="item.name"
                        :value="item._id"
                      ></el-option>
                    </el-select>
                    <el-input v-model="item.num" style="width:100px"></el-input>
                  </el-form-item>
                </el-col>
              </el-col>
            </el-col>
          </el-row>
        </el-tab-pane>
        <!--  3 -->
        <el-tab-pane label="3">
          <el-row type="flex" style="flex-wrap:wrap; margin-top:15px; ">
            <el-col style="border:none;">
              3  
              <el-button @click="model.items3.push({})">
                <i class="el-icon-plus"></i>
              </el-button>
              <el-col v-for="(item,i) in model.items3" :key="i">
                <el-col style="border:none;">
                  <el-form-item :label="`3 ${i+1}`">
                    <el-select
                    filterable
                      v-model="item.article"
                      placeholder=""
                      style="width:200px;margin-right:100px"
                    >
                      <el-option
                        v-for="item in articles"
                        :key="item._id"
                        :label="item.name"
                        :value="item._id"
                      ></el-option>
                    </el-select>
                    <el-input v-model="item.num" style="width:100px"></el-input>
                  </el-form-item>
                </el-col>
              </el-col>
            </el-col>
          </el-row>
        </el-tab-pane>
        <!--  4 -->
        <el-tab-pane label="4">
          <el-row type="flex" style="flex-wrap:wrap; margin-top:15px; ">
            <el-col style="border:none;">
              4  
              <el-button @click="model.items4.push({})">
                <i class="el-icon-plus"></i>
              </el-button>
              <el-col v-for="(item,i) in model.items4" :key="i">
                <el-col style="border:none;">
                  <el-form-item :label="`4 ${i+1}`">
                    <el-select
                    filterable
                      v-model="item.article"
                      placeholder=""
                      style="width:200px;margin-right:100px"
                    >
                      <el-option
                        v-for="item in articles"
                        :key="item._id"
                        :label="item.name"
                        :value="item._id"
                      ></el-option>
                    </el-select>
                    <el-input v-model="item.num" style="width:100px"></el-input>
                  </el-form-item>
                </el-col>
              </el-col>
            </el-col>
          </el-row>
        </el-tab-pane>
        <!--  5 -->
        <el-tab-pane label="5">
          <el-row type="flex" style="flex-wrap:wrap; margin-top:15px; ">
            <el-col style="border:none;">
              5  
              <el-button @click="model.items5.push({})">
                <i class="el-icon-plus"></i>
              </el-button>
              <el-col v-for="(item,i) in model.items5" :key="i">
                <el-col style="border:none;">
                  <el-form-item :label="`5 ${i+1}`">
                    <el-select
                      v-model="item.article"
                      placeholder=""
                      style="width:200px;margin-right:100px"
                    >
                      <el-option
                        v-for="item in articles"
                        :key="item._id"
                        :label="item.name"
                        :value="item._id"
                      ></el-option>
                    </el-select>
                    <el-input v-model="item.num" style="width:100px"></el-input>
                  </el-form-item>
                </el-col>
              </el-col>
            </el-col>
          </el-row>
        </el-tab-pane>
      </el-tabs>

      <el-form-item>
        <el-button type="primary" native-type="submit"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import {
  PutCharacterModel,
  postCharacter,
  getCharacterModel,
  getCategoryList,
  getCharacterList,
  getArticleList,
} from "@/api/api";
export default {
  props: ["id"],
  data() {
    return {
      model: {
        name: "",
        avatar: "",
        // 
        drawing: "",
        title: "",
        //  Cannot read property 'attack' of undefined
        //  underfind underfind.         model    Object.assign      
        hpandattack: {},
        // 
        partners: [],
        // 
        otherinfo: {},
        // 
        skills: [],
        // 
        attribute: {},
        // item1
        items1: [],
        items2:[],
        items3:[],
        items4:[],
        items5:[],
      },
      // 
      attributes: [],
      // 
      categories: [],
      // 
      races: [],
      // 
      characteies: [],
      // 
      articles: [],
    };
  },
  methods: {
    // 
    afterUpload(res) {
      // console.log(res);
      this.model.avatar = res.url;
    },
    // 
    afterUploadDrawing(res) {
      // console.log(res);
      this.model.drawing = res.url;
    },

    // 
    async fetch() {
      // const res = await this.$http.get(`categories/${this.id}`);
      const res = await getCharacterModel(this.id);
      this.model = Object.assign({}, this.model, res.data);
    },
    // 
    fetchCreate() {
      this.model = {};
    },
    // 
    async fetchAttribute() {
      // const res = await this.$http.get(`categories/${this.id}`);
      const res = await getCategoryList();
      this.attributes = res.data;
    },
    // 
    async fetchCategory() {
      // const res = await this.$http.get(`categories/${this.id}`);
      const res = await getCategoryList();
      this.categories = res.data;
    },
    // 
    async fetchArticle() {
      const res = await getArticleList();
      this.articles = res.data;
    },
    // ******   
    async fetchRace() {
      // const res = await this.$http.get(`categories/${this.id}`);
      const res = await getCategoryList();
      this.races = res.data;
    },
    // 
    async fetchCharacter() {
      // const res = await this.$http.get(`categories/${this.id}`);
      const res = await getCharacterList();
      this.characteies = res.data;
    },

    //  axios    ()
    async save() {
      // let res
      if (this.id) {
        // await this.$http.put(`categories/${this.id}`, this.model);
        // 
        // id 
        await PutCharacterModel(this.id, this.model);
        this.$message({
          type: "success",
          message: "",
        });
      } else {
        // await this.$http.post('categories', this.model);
        // 
        await postCharacter(this.model);
        this.$message({
          type: "success",
          message: "",
        });
      }

      this.$router.push("/characters/list");
    },
  },
  watch: {
    "$route.path": function () {
      this.fetchCreate();
    },
  },
  created() {
    this.fetchRace();
    this.fetchAttribute();
    this.fetchCategory();
    this.fetchCharacter();
    this.fetchArticle();
    this.id && this.fetch();
  },
};
</script>

<style>
.el-button.skills-button {
  margin-top: 2rem;
}
.el-col {
  margin: 40px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}

.el-col .no-border {
  border: none;
  display: inline-block;
}

.el-col .no-border .el-input {
  width: 200px;
}

.el-button {
  margin-top: 0.5rem;
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.el-upload .el-upload--text .avatar {
  width: 100%;
  height: auto;
  display: block;
}
.el-input {
  width: 300px;
}
.el-input.input_left {
  margin-right: 20px;
}
</style>