<template>
  <div class="about">
    <h1>{{id?'':''}}</h1>
    <el-form
      label-width="70px"
      @submit.native.prevent="save"
    >
      <el-form-item label="">
        <el-select
          v-model="model.parent._id"
          placeholder=""
        >
          <el-option
            v-for="item in parents"
            :key="item._id"
            :label="item.name"
            :value="item._id"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="model.name"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button
          type="primary"
          native-type="submit"
        ></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { getCategoryModel, putCategoryModel, postCategoryModel, getCategoryFatherList } from '@/api/api'
export default {
  props: ['id'],
  data() {
    return {
      model: {
        parent:{},
        name:''
      },
      parents: []
    }
  },
  methods: {
    // 
    async fetch() {
      // const res = await this.$http.get(`categories/${this.id}`);
      const res = await getCategoryModel(this.id)
      this.model = Object.assign({},this.model,res.data)
    },
    // 
    async fetchParents() {
      const res = await getCategoryFatherList();
      console.log(res);
      this.parents = res.data;
    },
    //  axios    ()
    async save() {
      // let res
      if (this.id) {
        // await this.$http.put(`categories/${this.id}`, this.model);
        // 
        // id 
        await putCategoryModel(this.id, this.model)
        this.$message({
          type: 'success',
          message: ''
        })
      } else {
        // await this.$http.post('categories', this.model);
        // 
        await postCategoryModel(this.model)
        this.$message({
          type: 'success',
          message: ''
        })
      }

      this.$router.push('/categories/list')
    }
  },
  created() {
    this.fetchParents()
    this.id && this.fetch();
  },
}
</script>